import Link from 'next/link';

export default function PropertyCard({ prop }) {
  return (
    <div style={{ border: '1px solid #ddd', padding: 12, borderRadius: 6 }}>
      <img src={prop.mainImage || '/placeholder.png'} alt={prop.title} style={{ width: '100%', height: 180, objectFit: 'cover', borderRadius: 6 }} />
      <h3>{prop.title}</h3>
      <p>{prop.location}</p>
      <p>â‚¹ {prop.price}</p>
      <Link href={`/property/${prop.id}`}><a>View</a></Link>
    </div>
  );
}
